a:link, a:hover, a:visited, a:active, a {
    text-decoration:none;
    color:black;
}

.foto{
    height: 150px;
    width: 150px;
    border-radius: 100%;
}

.perfil{
    text-align: center;
    padding-top: 50px;
    background-color: #2f2f2f;
    color: white;
}

.perfil h3{
    font-family: "Copperplate Gothic Light";
}

.footer{
    position: fixed;
    bottom: 0;
    width: 25%;
    text-align: center;
    font-family: Pristina;
}

.iconosPerfil{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
}

.iconosPerfil a i {
    padding-bottom: 5px;
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.iconosPerfil a i:hover {
    border-bottom: 2px solid white;
}

.descripcion{
    font-family: "AR ESSENCE";
}

@media only screen and (max-width: 800px) {
    .perfil{
        padding-bottom: 50px;
    }

    .footer{
        display: none;
    }
}
.opciones{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    text-align: center;
    justify-items: center;
    -webkit-align-items: center;
            align-items: center;
    padding-bottom: 15px;
    padding-top: 15px;
    margin: 0;
}

.opciones li{
    display: inline;
    height: 100%;
    width: 100%;
}

.opciones li a{
    text-decoration: none;
    white-space: nowrap;
}

.opciones li a:hover{
    font-weight: bold;
    padding-bottom: 14px;
    border-bottom: 3px solid black;
    margin: 0;
}

.barra{
    width: 75%;
    position: fixed;
    height: auto;
    margin-left: 25%;
    background-color: white;
}

@media only screen and (max-width: 800px) {
    .barra{
        width: 100%;
        margin: 0;
        position: static;
    }

    .opciones{
        padding-left: 0;
    }
}
