.contenido{
    margin-top: 54px;
    overflow: scroll;
    overflow-x: hidden;
    text-align: center;
}

h2{
    padding: 65px 0;
    margin: 0;
    color: aliceblue;
    font-family: "AR JULIAN";
    font-size: 35px;

    /*Efecto parallax*/
    background-image: url(/portfolio/static/media/binary-earth.802fd148.jpg);
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.contenidoSobremi{
    background-color: #474e5d;
    color: white;
    padding: 80px 15px;
}

.grupoProyectos{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-items: center;
    grid-row-gap: 30px;
    row-gap: 30px;
    padding-bottom: 80px;
    padding-top: 80px;
}

.conocimientos{
    list-style: none;
    border: 2px solid black;
    width: 250px;
    margin: 50px auto;
    padding: 0;
    border-radius: 10px;
    font-family: fantasy;
}

.conocimientos li{
    padding: 8px;
}

.conocimientos li:first-child{
    border-radius: 7px 7px 0px 0px;
}

.conocimientos li:last-child{
    border-radius: 0px 0px 7px 7px;
}

.conocimientos li:nth-child(odd){
    background-color: #0e132e;
}

.conocimientos li:nth-child(even){
    background-color: lightgreen;
    color: black;
}

.conocimientos li i {
    float: left;
}

.conocimientos li img {
    float: left;
}

.botonContenido{
    color: white !important;
    text-decoration: none;
    background-image: -webkit-linear-gradient(top left, gold, black);
    background-image: linear-gradient(to bottom right, gold, black);
    padding: 15px;
    border-radius: 5px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.botonContenido:hover{
    box-shadow: 3px 3px 5px gold;
}

.contenidoBlog{
    padding: 80px 15px;
    background-color: #0e132e;
    color: white;
    height: 100%;
    font-family: "Berlin Sans FB";
}

.parrafoBlog{
    padding-bottom: 40px;
}

.contenidoContacto{
    background-color: #1abc9c;
    padding-top: 100px;
    padding-bottom: 100px;
    color: white;
}

.contenidoContacto p{
    padding-top: 40px;
    padding-bottom: 15px;
}

#copiarCorreo{
    margin-left: 15px;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

#copiarCorreo:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.iconosContacto{
    max-width: 500px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin: 0 auto;
    justify-items: center;
}

.iconosContacto a {
    width: 35px;
    height: 40px;
}

.iconosContacto a i {
    text-decoration: none;
    -webkit-transform: scale(2);
            transform: scale(2);
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.iconosContacto a i:hover {
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
}

.palabraDestacada{
    color: gold;
}

.mensaje{
    margin-top: 35px;
}

.mensajeCopiado{
    border: 3px dashed greenyellow;
    padding: 5px;
    border-radius: 5px;
    font-size: 17px;
}

.footerAbsoluto{
    display: none;
    padding: 10px 0;
}

@media only screen and (max-width: 1200px) {
    .grupoProyectos{
        grid-template-columns: 1fr 1fr;
    }
}

@media only screen and (max-width: 800px) {
    .grupoProyectos{
        grid-template-columns: 1fr;
    }

    .contenido{
        margin-top: 0;
        overflow: hidden;
    }

    .footerAbsoluto{
        display: block;
        width: 100%;
        bottom: 0;
        font-family: Pristina;
        color: white;
        background-color: #2f2f2f;
    }
}

@media only screen and (max-width: 600px){
    h2{
        background-attachment: scroll;
    }
}
@font-face {
    font-family: 'Copperplate Gothic Light';
    src: url(/portfolio/static/media/COPRGTL.a2ef565d.eot);
    src: local('COPRGTL'),
    url(/portfolio/static/media/COPRGTL.a2ef565d.eot?#iefix) format('embedded-opentype'),
