}

.proyecto{
    float: left;
    width: 300px;
}

.contenidoProyecto{
    border: 2px solid black;
    border-radius: 5px;
    height: 250px;
    box-shadow: 3px 3px 10px 2px grey;
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.contenidoProyecto:hover{
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: lightseagreen;
}

.imagen{
    width: 100%;
    height: 180px;
    margin-top: 5px;
}

.encabezado{
    font-size: 18px;
    margin-top: 5px;
}

.lenguaje{
    float: left;
    margin-left: 5px;
    font-weight: bold;
}

.year{
    float: right;
    margin-right: 5px;
}

.visitar{
    float: left;
    margin-left: 5px;
    border: 1px solid black;
    border-radius: 3px;
    padding: 2px 8px;
}

.visitar:hover{
    background-color: darkblue;
    color: white;
}

.github{
    float: right;
    margin-right: 5px;
    padding-top: 3px;
}

.github:hover{
    color: white;
}

.titulo{
    font-weight: bold;
    font-family: "Copperplate Gothic Light";
}

.noweb{
    float: left;
    margin-left: 10px;
    padding-top: 3px;
    color: darkred;
    font-weight: bold;
}

@media only screen and (max-width: 500px) {
    .contenidoProyecto:hover{
        background-color: lightseagreen;
        -webkit-transform: none;
                transform: none;
    }
}
